import React, { Component } from 'react';
//import 'draft-js/dist/Draft.css';
import { Map } from 'immutable';
import Draft, {Editor, EditorState, RichUtils, convertToRaw } from 'draft-js';
import {stateToHTML} from 'draft-js-export-html';
import InlineStyleControls from './InlineStyleControls';
import BlockStyleControls from './BlockStyleControls';
import { getBlockStyle } from './BlockStyleFn';



class RichEditor extends React.Component {

  constructor(props) {
    super(props);
    this.state = {editorState: EditorState.createEmpty()};
  }

  focus = () => {
    this.refs.editor.focus();
  };

  onChange = (editorState) => {
    this.setState({editorState});
  };


  handleKeyCommand = (command, editorState) => {
    const newState = RichUtils.handleKeyCommand(editorState, command);
    if (newState) {
      this.onChange(newState);
      return true;
    }
    return false;
  };

  onTab = (e) => {
    const maxDepth = 4;
    this.onChange(RichUtils.onTab(e, this.state.editorState, maxDepth));
  };

  toggleBlockType = (blockType) => {
    this.onChange(
      RichUtils.toggleBlockType(
        this.state.editorState,
        blockType
      )
    );
  };

  toggleInlineStyle = (inlineStyle) => {
    this.onChange(
      RichUtils.toggleInlineStyle(
        this.state.editorState,
        inlineStyle
      )
    );
  };

  render() {
      const TOHTML_OPTIONS = {
          // blockRenderers: {
          //     'center': (block) => {
          //         console.log(block);
          //         return null;
          //     },
          // },
          // defaultBlockTag: 'center',
          inlineStyles: {
              // Override default element (`strong`).
              BOLD: {element: 'strong'},
              ITALIC: {element: 'i'},
          },
      };

      const blockRenderMap = Map({
          'center': {
              element: 'section'
          },
          'header-one': {
              element: 'h5'
          }
      });
      const extendedBlockRenderMap = Draft.DefaultDraftBlockRenderMap.merge(blockRenderMap);
    // If the user changes block type before entering any text, we can
    // either style the placeholder or hide it. Let's just hide it now.
    let className = 'RichEditor-editor';
    var contentState = this.state.editorState.getCurrentContent();
    if (!contentState.hasText()) {
      if (contentState.getBlockMap().first().getType() !== 'unstyled') {
        className += ' RichEditor-hidePlaceholder';
      }
    }
    console.log(convertToRaw(this.state.editorState.getCurrentContent()));
    //console.log(extendedBlockRenderMap);
    return (
      <div>
      <div className="RichEditor-root">
        <BlockStyleControls
          editorState={this.state.editorState}
          onToggle={this.toggleBlockType}
        />
        <InlineStyleControls
          editorState={this.state.editorState}
          onToggle={this.toggleInlineStyle}
        />
        <div className={className} onClick={this.focus}>
          <Editor
            blockStyleFn={getBlockStyle}
            editorState={this.state.editorState}
            handleKeyCommand={this.handleKeyCommand}
            onChange={this.onChange}
            onTab={this.onTab}
            placeholder="Tell a story..."
            ref="editor"
            spellCheck={true}
            blockRenderMap={extendedBlockRenderMap}
            //blockStyleFn={myBlockStyleFn}
          />
        </div>
        <div>
        </div>
      </div>
          { stateToHTML(this.state.editorState.getCurrentContent(), TOHTML_OPTIONS) }
          <div className="reTextCenter">
            Hello
          </div>
      </div>
    );
  }
}

RichEditor.propTypes = {};

export default RichEditor;



